import Vue from 'vue'
import VueRouter from 'vue-router'
import { Message, Loading } from 'element-ui'
// import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
    // {
    //   path: '/',
    //   name: 'Home',
    //   component: Home
    // },
    // {
    //   path: '/about',
    //   name: 'About',
    //   // route level code-splitting
    //   // this generates a separate chunk (about.[hash].js) for this route
    //   // which is lazy-loaded when the route is visited.
    //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    // }
    {
        path: '/',
        redirect: '/loginandregist'
    },
    {
        path: '/404',
        name: 'Error',
        component: () =>
            import ('../views/Error.vue')
    },
    {
        path: '/loginandregist',
        name: 'LoginAndRegist',
        component: () =>
            import ('../views/LoginAndRegist.vue')
    },
    {
        path: '/index',
        name: 'Index',
        redirect: '/index/tutorial',
        component: () =>
            import ('../views/Index.vue'),
        children: [{
                path: '/index/tutorial',
                name: 'Tutorial',
                component: () =>
                    import ('../components/IndexComponets/Tutorial.vue')
            },
            {
                path: '/index/userInfo',
                name: 'UserInfo',
                component: () =>
                    import ('../components/IndexComponets/UserInfo.vue')
            },
            {
                path: '/index/bulletin',
                name: 'Bulletin',
                component: () =>
                    import ('../components/IndexComponets/Bulletin.vue')
            },
            {
                path: '/index/problem/:problem',
                name: 'Problem',
                component: () =>
                    import ('../components/IndexComponets/Problem.vue')
            },
            {
                path: '/index/answer/:id',
                name: 'Answer',
                component: () =>
                    import ('../components/IndexComponets/Answer.vue')
            }
        ]
    },
    {
        path: '*',
        redirect: '/404'
    }

]

const router = new VueRouter({
    routes
})

router.beforeEach((to, from, next) => {
    let userInfo = JSON.parse(localStorage.getItem('userInfo'));
    // let token = userInfo.token;
    if (to.path != '/loginandregist' && !userInfo) {
        next({
            path: '/loginandregist'
        });
        Message({
            type: 'error',
            message: '请您先进行登录!'
        })
    } else
        next();
})

export default router